* {
	margin: 0;
	padding: 0;
	box-sizing: border-box;
	
}

#root {
	overflow: hidden;
	display: flex;
	flex-direction: column;
	align-items: center;
}

.App {
	width: 800px;

}

.post {
	display: flex;
	padding: 15px;
	border: 2px solid teal;
	margin-top: 15px;
	justify-content: space-between;
	align-items: center;
}

.post__btn {
	display: flex;
}

.nav__btn {
	padding: 5px 15px;
	color: teal;
	font-size: 14px;
	background: transparent;
	border: 1px solid teal;
	cursor: pointer;
	text-decoration: none;
}

.page__wrapper {
margin-top: 30px;
display: flex;
}

.page {
padding: 10px;
border: 1px solid teal;
cursor: pointer;
}

.page__current {
	border: 2px solid orange;
	font-weight: bold;
}

.navbar {
	height: 50px;
	width: 100vw;
	display: flex;
	align-items: center;
	padding: 0 15px;
	background: lightgray;
}

.navbar__links {
	margin-left: auto;
}